.home__body {
  background-image: url(/src/starter-code/assets/home/background-home-desktop.jpg);
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 6rem;
}

.home__container {
  margin-top: 20vh;
  height: 65vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
}

.home__container:nth-child(1) {
  width: 38vw;
  margin-left: 15rem;
}

h2.home__title {
  font-size: 2.5rem;
  font-weight: 300;
  letter-spacing:5px;
  text-align: left;
  text-transform: uppercase;
  width: 100%;
}

h1.home__title {
  font-size: 11rem;
  font-weight: 500;
  font-family: var(--Font-Title);
  margin-top: 1rem;
  width: 100%;
  color: white;
}

.home__paragraph {
  margin-top: 2rem;
  font-size: 1.75rem;
  line-height: 2.5rem;
  font-weight: 300;
  text-align: justify;
}

.explore {
  background-color: white;
  color: black;
  text-decoration: none;
  font-size: 2.5rem;
  font-family: var(--Font-Title);
  text-align: center;
  text-transform: uppercase;
  line-height: 20rem;
  height: 20rem;
  width: 20rem;
  border-radius: 50%;
  transition: all .3s;
}

.explore:hover {
  transform: scale(1.2) translateY(-2rem);
  box-shadow: 0 0 1rem 1rem var(--Decoration-Gray);
}

@media screen and (max-width:1400px) {
  h1.home__title {
    font-size: 10rem;    
  }
}
@media screen and (max-width:1300px) {
  h1.home__title {
    font-size: 9rem;    
  }
}
@media screen and (max-width:1200px) {
  h1.home__title {
    font-size: 8rem;    
  }
}
@media screen and (max-width:1100px) {
  h1.home__title {
    font-size: 7rem;    
  }
}
@media screen and (max-width:900px) {
  h1.home__title {
    font-size: 4rem;    
  }
}

@media screen and (max-width: 800px) {
  .home__body {
    background-image: url(/src/starter-code/assets/home/background-home-tablet.jpg);
    grid-template-columns: 1fr;
    gap: 0rem;
    margin-top: -7rem;
    height: auto;
  }
  
  .home__container {
    margin-top: 0;
    height: auto;
    text-align: center;
    justify-content: center;
  }
  
  .home__container:nth-child(1) {
    width: 90%;
    margin: 10rem auto 0;
  }
  
  h2.home__title {
    text-align: center;
  }
  
  h1.home__title {
    font-size: 10rem;
    margin-top: -1rem;
  }
  
  .home__paragraph {
    font-size: 2rem;
    text-align: center;
    width: 90%;
  }
  
  .explore {
    font-size: 3rem;
    margin: 2rem auto;
    transition: none;
  }
  
  .explore:hover {
    transform: scale(1) translateY(0rem);
    box-shadow: none;
  }
}

@media screen and (max-width: 550px) {
  body, html {
    min-height: 100vh;
  }
  .home__body {
    background-image: url(/src/starter-code/assets/home/background-home-mobile.jpg);
    height: fit-content;
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
  
  .home__container:nth-child(1) {
    width: 95%;
  }
  
  h2.home__title {
    font-size: 2rem;
    letter-spacing: 3px;
  }
  
  h1.home__title {
    font-size: 6rem;
    letter-spacing: -5px;
  }

  .explore {
    margin: 2rem auto 20rem;
  }
}
